@import 'fancy-menu-icon';

header {
	color: $text-color;
	background: #fff;
	position: fixed;
	top: 0;
	width: 100%;
	z-index: 10;
	height: $collapsed-header-height;
	@include transition((height $header-transition-speed $ease, background $header-transition-speed $ease));
	@include respond($md) {
		height: 120px;
	}
	@include respond($lg) {
		height: $header-height;
	}
	.container {
		height: $collapsed-header-height;
		top: 50%;
		margin-top: -($collapsed-header-height/2);
		@include respond($lg) {
			width: 100%;
		}
	}
	.menu-link {
		@include respond($lg) {
			display: none;
		}
	}
	.logo {
		display: block;
		width: 76px;
		color: transparent;
		padding: 0;
		@include transition(height $header-transition-speed $ease);
		z-index: 2;
		border: none;
		margin: 0 auto;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		height: $collapsed-header-height - 30px;
		.mixers {
			display: none;
			@include respond($md) {
				display: block;
			}
		}
		@include respond($md) {
			height: 60px;
		}
		@include respond($lg) {
			height: 86px;
		}
		svg {
			height: 100%;
			width: 100%;
			transform: translate3d(0, 13%, 0);
			transition: all $header-transition-speed $ease;
			@include respond($md) {
				transform: translate3d(0, 0, 0);
				height: 100%;
			}
			* {
				fill: $text-color;
			}
			.mixers {
				opacity: 1;
			}
			@include respond($lg) {
				
			}
		}
	}

	nav {
		display: none;
		@include respond($lg) {
			display: block;
			@include clearfix();
		}
		a {
			border: none;
			text-transform: uppercase;
			letter-spacing: $very-tracked-out;
			font-weight: 700;
			font-size: 10px;
			display: block;
			text-align: center;
			position: relative;
			cursor: pointer;
			&:after {
				@include respond($lg) {
					content: '';
					display: block;
					height: 1px;
					left: 0;
					right: .15em;
					bottom: 50%;
			    margin-bottom: -1em;
					background: $text-color;
					position: absolute;
					transform: translate3d(0, 8px, 0);
					opacity: 0;
					transition: opacity $med-speed $ease, transform $med-speed $ease, background $med-speed $ease;
				}
			}
			&:hover {
				&:after {
					transform: translate3d(0, 0, 0);
					opacity: 1;
				}
			}
			@include respond($lg) {
				display: inline-block;
				height: 76px;
				padding: 32px 0 32px;
				margin: 0;
				line-height: 1em;
			}
			@include respond($xlg - 100) {
				font-size: 12px;
			}
		}
		.left {
			display: inline;
			@include respond($lg) {
				padding-right: 80px;
				text-align: right;
				width: 50%;
				display: inline-block;
				display: block;
				position: absolute;
				left: 0;
				top: 0;
				z-index: 1;
			}
			@include respond($huge) {
				margin-right: 115px;
			}
			a {
				@include respond($lg) {
					margin-left: 3.2em;
					&:first-child {
						margin-left: 0;
					}
				}
			}
		}
		.right {
			display: inline;
			@include respond($lg) {
				padding-left: 75px;
				text-align: left;
				width: 50%;
				display: inline-block;
				display: block;
				position: absolute;
				right: 0;
				top: 0;
				z-index: 1;
			}
			@include respond($huge) {
				margin-left: 115px;
			}
			a {
				@include respond($lg) {
					margin-right: 2.4em;
					&:last-child {
						margin-right: 0;
					}
				}
			}
		}
	}

	&.inverted {
		background: transparent;
		a {
			color: #fff;
			&:after {
				background: #fff;
			}
		}
		.logo {
			svg {
				* {
					fill: #fff;
				}
			}
		}
		.menu-icon {
			&:before,
			&:after,
			.center,
			.center:before {
				background: #fff;
			}
		}
	}

	&.transparent {
		background: transparent;
	}

}

header.collapsed,
header.inverted.collapsed,
header.inverted.transparent {
	background: #fff;
	height: 60px;
	@include respond($md) {
		height: $collapsed-header-height;
	}
	a {
		color: $text-color;
		&:after {
			background: $text-color;
		}
	}
	.logo {
		height: 46px;
		@include respond($md) {
			height: $collapsed-header-height - 22px;
		}
		svg {
			transform: translate3d(0, 13%, 0);
			* {
				fill: $text-color
			}
			.mixers {
				opacity: 0;
				transform: translate3d(0, -20px, 0);
			}
		}
	}
	.menu-icon {
		&:before,
		&:after,
		.center,
		.center:before {
			background: $text-color;
		}
	}
}